























import Vue from "vue";
export default Vue.extend({
  props: {
    url: {
      type: String,
      required: false,
    },
    del: {
      type: Boolean,
      required: true,
    },
  },
  methods: {
    goImage() {
      if (!this.del) {
        open("http://localhost:8001" + this.url);
      }
    },
  },
});
