






import Vue from "vue";
import { manageError } from "../util/func";

export default Vue.extend({
  created() {
    this.getUser();
  },
  methods: {
    async getUser() {
      try {
        const res = await this.$store.dispatch("FETCH_USER");

        if (res.status == 200) {
          location.href = "http://localhost:8080/";
        }
      } catch (e) {
        console.log(e);
      }
    },
  },
});
