






















































import Vue from "vue";
import { prayApi } from "../api";
import { manageError } from "../util/func";

export default Vue.extend({
  data() {
    return {
      list: null,
      isThere: false,
      loading: false,
      reLoading: false,
    };
  },
  async created() {
    try {
      const { data } = await prayApi.getPraysByUserId(
        parseInt(this.$route.params.id, 10),
        0
      );
      this.loading = true;
      this.list = data.meta;
      if (this.list.length == 5) {
        this.isThere = true;
      }
    } catch (e) {
      const error = e.toString().substring(e.toString().length - 3);
      manageError(error);
    }
  },
  methods: {
    async addList() {
      try {
        this.reLoading = true;
        const { data } = await prayApi.getPraysByUserId(
          parseInt(this.$route.params.id, 10),
          this.list[this.list.length - 1].id
        );
        this.reLoading = false;

        if (data.meta.length != 5) {
          this.isThere = false;
        }
        this.list = [...this.list, ...data.meta];
      } catch (e) {
        const error = e.toString().substring(e.toString().length - 3);
        manageError(error);
      }
    },
  },
});
