










































































import Vue, { PropType } from "vue";
import User from "../types/user";
import { prayApi } from "../api";
import { manageError } from "../util/func";

export default Vue.extend({
  props: {
    user: {} as PropType<User>,
  },
  data() {
    return {
      hasSaved: false,
      isEditing: null,
      list: [],
      addLoading: false,
    };
  },

  methods: {
    async addItem(UserId: number) {
      try {
        this.addLoading = true;
        const { data } = await prayApi.createPray(
          UserId,
          "새로운 기도제목 입니다."
        );
        this.list = [
          ...this.list,
          {
            content: "새로운 기도제목입니다.",
            edit: false,
            id: data.meta,
            deleteLoading: false,
            updateLoading: false,
          },
        ];
      } catch (e) {
        const error = e.toString().substring(e.toString().length - 3);
        manageError(error);
      } finally {
        this.addLoading = false;
      }
    },
    async updateItem(id: number, index: number) {
      try {
        this.list.splice(index, 1, {
          ...this.list[index],
          updateLoading: true,
        });
        const { data } = await prayApi.updatePray(id, this.list[index].content);
        this.list.splice(index, 1, {
          ...this.list[index],
          updateLoading: false,
          edit: false,
        });
      } catch (e) {
        const error = e.toString().substring(e.toString().length - 3);
        manageError(error);
      }
    },
    async deleteItem(id: number, index: number) {
      try {
        this.list.splice(index, 1, {
          ...this.list[index],
          deleteLoading: true,
        });
        const { data } = await prayApi.deletePray(id);
        this.list.splice(index, 1);
      } catch (e) {
        const error = e.toString().substring(e.toString().length - 3);
        manageError(error);
      }
    },
  },

  created() {
    this.user.Prays.map((e) => {
      this.list = [
        ...this.list,
        { ...e, edit: false, deleteLoading: false, updateLoading: false },
      ];
    });
    this.list = this.list.filter((e) => e.content != "default");
  },
});
